
/*
1.$변수명 : 변수에 넣을 값
2. @import 파일경로
3.reset.scss파일 같은경로는 파일명은 _reset.scss라고쓴다(전역에서 다 쓸거니깐)
4.nesting >> 
    div container {
        div {
            color : red
        }
        p {
            color : blue
        }
    }
5.@extend : extend는 css설정을 extend하는 설정이다 @extend하면 끝
6.@mixin : 함수만드는 문법
7.@include
*/

//변수저장
$메인칼라 : #ff0000;

.red{
    color : $메인칼라;
}

@mixin 함수() {
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert{
   @include 함수()
}

.my-alert2 {
    @extend .my-alert;
    background: #ffe591;
}

.my-alert p {
    margin-bottom: 0;
}
.btn btn-red{
    display: none;
}

